import Toast from '@/wxcomponents/vant-weapp/dist/toast/toast';
export default {
	data() {
		return {
			isLogin: true,
			username: '',
			password: '',
			againPwd: ''
		}
	},
	methods: {
		// 登录
		loginByAccount() {
			if (this.username == '' || this.password == '') {
				Toast('请输入用户名或者密码');
				return
			}
			this.$minApi.loginByAccount({
				password: this.password,
				username: this.username,
				userSchoolId: '1',
				userIdenty: '0'
			}).then(data => {
				if (data.code == 200) {
					uni.setStorageSync('token', data.data.token),
						uni.switchTab({
							url: '../../my/index'
						})
						Toast(data.msg);
				} else {
					Toast(data.msg);
				}

			})
		},
	},
	created() {

	}
}
